<script>
export default {
  name: 'AppNavbar',
  data() {
    return {
      isLogin: false,
      isToggleNavbar: false,
      isToggleDropdown: {
        production: false,
        media: false,
        introduction: false,
        support: false,
      },
    };
  },
  methods: {
    toggleNavbar() {
      this.isToggleNavbar = !this.isToggleNavbar;
    },
    closeNavbar() {
      this.isToggleNavbar = false;
      this.isToggleDropdown = {
        production: false,
        media: false,
        introduction: false,
        support: false,
      };
    },
    clickDropdown(key) {
      this.isToggleDropdown[key] = this.isToggleDropdown[key] === false;
    },
    blurDropdown(key) {
      setTimeout(() => {
        this.isToggleDropdown[key] = false;
      }, 30);
    },
  },
  watch: {
    $route: 'closeNavbar',
  },
};
</script>

